.loading {
  width: 100%;
  padding: 15px 0;
  background-color: #fff; }
  .loading .loading-circle {
    position: relative;
    width: 20px;
    height: 20px;
    border: 1px solid #555;
    border-radius: 50%;
    margin: 0 auto;
    -webkit-animation: loadingRound 2s ease-in-out infinite forwards;
    animation: loadingRound 2s ease-in-out infinite forwards; }
    .loading .loading-circle::after {
      content: "";
      position: absolute;
      height: 8px;
      width: 8px;
      top: -4px;
      left: 50%;
      margin-left: -4px;
      background: #555;
      border-radius: 100%; }

@-webkit-keyframes loadingRound {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@keyframes loadingRound {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }



