@charset "UTF-8";
html,
body {
  /* width: 100%;
  height: 100%; */
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

#root {
  /* width: 100%;
  height: 100%; */
  min-width: 1200px;
  margin: 0 auto;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  /* overflow: auto; */ }

ul {
  margin: 0;
  padding: 0; }

li {
  list-style: none; }

@font-face {
  font-family: "FedraSans";
  src: url("./font/FedraSa0.ttf"); }

@font-face {
  font-family: "Apercu";
  src: url("./font/apercu-light.woff"); }

/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
::-webkit-scrollbar {
  width: 4px;
  height: 4px;
  background-color: #F5F5F5; }

/*定义滚动条轨道 内阴影+圆角*/
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  /* border-radius: 10px; */
  background-color: #eee; }

/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #555; }



